        <el-drawer 
            :size="'350px'"
            :visible.sync="cartStatus"
            :before-close="shutCart"
            :with-header="false">
            <div  class="minicart active" id="minicart">
                <header> 
                    <div type="button"  id="cart-widget-close" class="cart-widget-close" @click="cartClose"> 
                        <img src="../../../assets/picture/icon-right.png" alt="">
                    </div>
                    
                    <div  class="minicart-title" id="minicart-title">
                        <span  class="user-free-text">CART</span>
                    </div>
                </header>
                <section class="cart-content">
                    <div  class="widget-empty-message">
                        <div class="mini-cart-empty-user-text">
                            <span  class="user-free-text">Cart is empty</span>
                        </div>
                    </div>
                    <!-- 购物车 -->
                    <div class="widget-message">
                        <div class="cart-container">
                            <div class="cart-content-list">
                                <ul>
                                    <li id="card1" v-for="item in cartList" :key="item.id">
                                        <img class="shoppic" src="../../../assets/picture/good1.png" width="80" height="80"/>
                                        <div class="item-info">
                                            <div id="item-name-0" class="item-name" data-hook="cart-widget-name">
                                                {{item.name}} 
                                            </div>
                                            <div class="item-price">
                                                <span >₱{{item.price}} </span>
                                            </div>
                                            <div class="product-quantity" >
                                                <div class="product-quantity-container"> 
                                                    <a id="shopreduce" @click="reduceCart(item)" class="change-quantity-icon minus-icon disabled"> 
                                                        - 
                                                    </a> 
                                                    <a id="shopadd" @click="addCart(item)" class="change-quantity-icon plus-icon" > 
                                                        +
                                                    </a> 
                                                    <input v-model="item.count" id="shopnum" class="product-quantity-input ng-pristine ng-untouched ng-valid ng-not-empty"> 
                                                </div>
                                            </div>
                                        </div> 
                                        <img src="../../../assets/picture/xx.png" class="deleteshop" @click="deleteshop(item.id)">
                                    </li>
                                </ul>
                            </div>
                        </div>
                        
                        <section class="cart-widget-subtotal">
                            <div class="cart-widget-subtotal-title"> Subtotal</div>
                            <div class="cart-widget-total"> ₱<span class="cart-widget-subtotal-title">{{totalPrices}} </span></div>
                        </section>
                    </div>
                </section>
                <footer class="footer"> 
                    <router-link id="widget-view-cart-button" to="/cart">
                        View Cart
                    </router-link> 
                </footer>
            </div>
        </el-drawer>
        
