<script>
    import {mapState,mapMutations} from 'vuex'
    export default {
        data() {
			return {
                // active:'Home',
                cartSize:350,
            }
        },
        computed:mapState({
            active: state => state.active,
            cartStatus: state => state.cartStatus,
            cartList: state => state.cartList,
            totalPrices: state => state.totalPrices,
        }),
        created(){
            this.getCartList();
        },
        methods:{
            ...mapMutations(['changeCartStatus','saveCartList','getCartList','deleteCartShop']),
            shutCart(){
                this.changeCartStatus(false);
            },
            cartButton(){
                this.changeCartStatus(true);
            },
            cartClose(){
                this.changeCartStatus(false);
            },
            pushIndex(el){
                localStorage.setItem('pushIndex',el);
                this.$router.push('/')
            },
            deleteshop(id){
                this.deleteCartShop(id)
            },
            reduceCart(item){
                if(item.count>1){
                    this.saveCartList({id:item.id,count:-1});
                }
            },
            addCart(item){
                let {id,name,price} = item;
                this.saveCartList({id,name,price,count:1});
            }
        }
    };
</script>

